import { observable, action } from 'mobx'
import { allList, removeData } from "@/api/index.js"
import {message} from "antd"

class Contorl {
    @observable staffList = ['总路由']

    @action.bound
    staffListOfAdd(data) {
        console.log(data);
        console.log(this.staffList);
    }

    @action.bound
    staffListOfDel(idArr) {
        removeData({ id: JSON.stringify(idArr) }).then(res => {
            let {code} = res;
            if(code){
                message.success('删除成功')
            }
        })
    }

    @action.bound
    getStaffListFn(limit, pageid) {
        allList({limit:limit,pageid:pageid}).then(res => {
            let {code,data} = res;
            if(code){
                
                this.staffList = data
            }
        })
    }
}

export default new Contorl;
