import request from "../untils/index";
export const login = (data) => request.post("/user/login", data) //登陆
//export const importXlsx = (data)=>request.post("/management/importXlsx",data) //用户管理导入接口


export const original = (data) => request.post("/management/exportXlsx/original", data, { responseType: "blob" }) //下载初始模板
export const pushData = (data) => request.post("/management/importXlsx", data) //上传文档
export const allList = (data) => request.get("/management/alldata", { params: data }) //获取所有数据
export const removeData = (data) => request.post("/management/removedata",data) //删除数据
export const addList = (data)=>request.post("/management/adddata",data) //添加数据
